import React from 'react';
import Layout from '../layout';
import { Breadcrumbs } from './breadcrumbs';
import { Link } from 'gatsby';
import SEO from '../seo';

export const GenericPage = ({ title, description, breadcrumbs, children, showBackButton }) => {
  return (<>
    <Layout>
      <SEO title={title} description={description}/>
      <div className="container" style={{ paddingTop: '12rem' }}>
        {showBackButton &&
          <Link to={breadcrumbs[breadcrumbs.length - 2].link} className='link'>
            <span className='icon back-blue-arrow mr-1'></span> Назад
          </Link>
        }

        <h1>{title}</h1>
        <Breadcrumbs items={breadcrumbs} />
        <hr className='normal-bottom' />
        <div className='col-12 col-lg-8 p-0 m-0 mb-5'>
          {children}
        </div>
      </div>
    </Layout>
  </>);
};
